import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { ERROR, InputGroupInline, InputType, REFERENCES, showToast, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { LivroFolha } from '../../../../../models/dtos/mcg/livroFolha';
import { CONSOLIDAR_ENUM_VALUES, NORMAL } from '../../../../../models/enumerated/mcg/consolidarEnum';
import { buscarConfigLivroFolhaMCG } from '../../../../../services/mcg.service';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, RazaoCentroCustoRV, REPORT_EVENT, REPORT_TITLE } from './razaoCentroCustoRV';

const DEFAULT_VALUES: RazaoCentroCustoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  consolidacao: NORMAL.value,
  exibirFormatacaoValor: true,
  imprimirHistoricos: true,
  virtualization: true,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCES.value),
  exibirConfiguracaoDadosEmpresa: false,
  exibirImprimirValoresColoridos: true,
};

const LalurLacsParteAReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<RazaoCentroCustoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [isEnabledIsMatriz, setIsEnabledIsMatriz] = useState(false);

  const carregarParametroEmpresa = () => {
    if (globalReducer.globalParameter.empresa.matriz === null) {
      setIsEnabledIsMatriz(true);
      formProps.getValues().isAcumularMatrizFilial = globalReducer.globalParameter.empresa.centralizar;
    }
  };

  const getInfoDiario = () => {
    const {
      empresa: { id },
    } = globalReducer.globalParameter;

    buscarConfigLivroFolhaMCG(id, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: (result: LivroFolha) => {
        setTimeout(() => {
          formProps.setValue('startPage', result.razaoFolha.toString(), true);
        }, 700);
      },
    });
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        setTimeout(() => {
          getInfoDiario();
          carregarParametroEmpresa();
        }, 1000);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const acumularMatrizFilial = isEnabledIsMatriz ? (
    <Col>
      <CustomInput
        type="switch"
        label="Acumular Matriz e Filiais"
        name="isAcumularMatrizFilial"
        id="isAcumularMatrizFilial"
        innerRef={formProps.register}
      />
    </Col>
  ) : null;

  return (
    <WindowComponent
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      wikiTitle={'Relacionamentos de Plano de Contas'}
    >
      <Row className="mb-3">
        <Col>
          <CustomInput id="imprimirHistoricos" label="Imprimir Históricos" name="imprimirHistoricos" innerRef={formProps.register} type="switch" />
        </Col>
        <Col>
          <CustomInput
            id="imprimirReferenciaPagina"
            label="Separar referências em páginas diferentes"
            name="imprimirReferenciaPagina"
            innerRef={formProps.register}
            type="switch"
          />
        </Col>
        {acumularMatrizFilial}
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="consolidacao"
            label="Consolidação"
            isPrependLabel={true}
            placeholder="Selecione uma consolidação"
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: CONSOLIDAR_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      />
    </WindowComponent>
  );
};

export default LalurLacsParteAReportView;
